import React from 'react'
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Button from 'muicss/lib/react/button';
import {CopyToClipboard} from 'react-copy-to-clipboard';



const ClgTitle = ({info}) => (

  <Container fluid={true} style={{marginTop:'22px'}}>
      <Row>
      <Col className="mui--text-headline" md="8">{info.nom} / {info.ville} </Col>
      <Col className="mui--text-caption " md="1"><div style={{borderStyle:'solid',borderWidth:'1px',borderRadius:'4px',borderColor:'#DDDDDD',fontSize:'10px',textAlign:'center',paddingTop:'2px'}}>{info.etat_pnc}</div></Col>
      <Col className="mui--text-subhead mui--text-right" md="3">
        <span style={{fontSize:'10px'}}>{info.numid}</span> <span style={{fontSize:'10px'}}>{info.abreviation}</span>
        <CopyToClipboard text={info.rne} >
        <Button variant="flat" size="small" >RNE: {info.rne}</Button>
      </CopyToClipboard>


    </Col>
      </Row>
  </Container>
)

export default ClgTitle
