import React from "react"
import { graphql } from 'gatsby'
import Layout from "../components/layout"

import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Divider from 'muicss/lib/react/divider';

import ClgTitle from '../components/clgtitle'
import ClgMenu from '../components/clgmenu'

import ClgRowLan from '../components/clgrowlan'

class ClgNetworkTemplate extends React.Component {

  render() {

    const lans = this.props.data.allResauxXlsxLocal.edges
    const csms = this.props.data.allResauxXlsxCsm.edges
    const info = this.props.data.clgListeXlsxRefClg

    return (<Layout>
      <ClgMenu id={info.rne} />
      <ClgTitle info={info}  />
      <Divider/>
        <Container fluid={true} style={{marginTop:'22px'}}>
          <Row>
            <Col md="12" className="mui--text-title">RESEAUX LOCAUX PNC</Col>
          </Row>
          <Row>
            <Col md="12">
              <table className="mui-table mui-table--bordered">
                <thead><tr><th>Nom</th><th>Vlan</th><th>Réseau</th><th>Masque</th><th>Passerelle</th><th>IPs</th></tr></thead>
                <tbody>
              {lans.map(function(lan, i){
                  return <ClgRowLan lan={lan.node} key={i} />;
              })}

            </tbody>
              </table>
            </Col>
          </Row>
          <Divider/>
          <Row style={{marginTop:'12px'}}>
            <Col md="12" className="mui--text-title">RESEAUX CSM</Col>
          </Row>

          <Row>
            <Col md="12">
              <table className="mui-table mui-table--bordered">
                <thead><tr><th>Nom</th><th>Vlan</th><th>Réseau</th><th>Masque</th><th>Passerelle</th><th>IPs</th></tr></thead>
                <tbody>
              {csms.map(function(lan, i){
                  return <ClgRowLan lan={lan.node} key={i} />;
              })}

            </tbody>
              </table>
            </Col>
          </Row>
      </Container>

    </Layout>)
  }
}

export default ClgNetworkTemplate

export const pageQuery = graphql `
    query($numid: Int!) {
      allResauxXlsxLocal(filter: {numid: {eq: $numid}}) {
        edges {
          node {
            numid
            block
            cidr
            gateway
            id
            ip
            ipnet
            ipnum
            mask
            size
            vlanid
            vlanname
          }
        }
      }

    allResauxXlsxCsm(filter: {numid: {eq: $numid}}) {
    edges {
      node {
        numid
        block
        cidr
        gateway
        id
        ip
        ipnet
        ipnum
        mask
        size
        vlanid
        vlanname
      }
    }
  }

clgListeXlsxRefClg(numid: {eq: $numid}) {
abreviation
ancien_nom
canton
categorie
date_pnc(formatString: "DD/MM/YY")
email_college
email_gestionnaire
etat_pnc
info_ac_nantes
nom
numid
rne
tel_college
titre
vague_pnc
ville
}
    }
  `
