import React from 'react'

import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Link from 'gatsby-link'


const ClgMenuItem = ({intitule,path}) => (

<Col md="1" className="mui--text-center" ><Link to={path}  >{intitule}</Link></Col>


)

const ClgMenu = ({id}) => (
  <Container fluid={true} style={{marginTop:'12px'}}>
    <Row>
      <ClgMenuItem intitule="Collège" path={"/"+id}/>
      <ClgMenuItem intitule="Tickets" path={"/"+id+"/tickets"}/>
      <ClgMenuItem intitule="Réseaux" path={"/"+id+"/reseaux"}/>

    <ClgMenuItem intitule="Telephonie" path={"/"+id+"/telephonie"}/>
    <ClgMenuItem intitule="Operateur" path={"/"+id+"/operateur"}/>
    </Row>
  </Container>
)

export default ClgMenu
